import React, { useEffect, useMemo, useState } from "react";
import "./ProjectsSlider.css";

import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Keyboard } from "swiper/modules";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import { FiEye, FiX, FiChevronLeft, FiChevronRight } from "react-icons/fi";

type ProjectImage = {
  src: string;
  alt: string;
};

const projectImages: ProjectImage[] = [
  {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img2.jpeg",
    alt: "Project 1",
  },
  {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img8.jpg",
    alt: "Project 2",
  },
  {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img7.jpg",
    alt: "Project 3",
  },
  {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img6.jpg",
    alt: "Project 4",
  },
  {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img5.jpg",
    alt: "Project 5",
  },
  {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img4.jpg",
    alt: "Project 6",
  },
    {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img3.jpeg",
    alt: "Project 7",
  },
   {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img9.jpg",
    alt: "Project 8",
   },
   {
    src: "https://ik.imagekit.io/dloitl7fd8/images/img1.jpeg",
    alt: "Project 9",
   }
];

export default function ProjectsSlider() {
  const [isOpen, setIsOpen] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);

  const activeImage = useMemo(() => projectImages[activeIndex], [activeIndex]);

  function openLightbox(index: number) {
    setActiveIndex(index);
    setIsOpen(true);
  }

  function closeLightbox() {
    setIsOpen(false);
  }

  function prevImage() {
    setActiveIndex((prev) => (prev - 1 + projectImages.length) % projectImages.length);
  }

  function nextImage() {
    setActiveIndex((prev) => (prev + 1) % projectImages.length);
  }

  // keyboard controls
  useEffect(() => {
    if (!isOpen) return;

    const onKeyDown = (e: KeyboardEvent) => {
      if (e.key === "Escape") closeLightbox();
      if (e.key === "ArrowLeft") prevImage();
      if (e.key === "ArrowRight") nextImage();
    };

    window.addEventListener("keydown", onKeyDown);
    return () => window.removeEventListener("keydown", onKeyDown);
  }, [isOpen]);

  // lock scroll when open
  useEffect(() => {
    if (!isOpen) return;
    document.body.style.overflow = "hidden";
    return () => {
      document.body.style.overflow = "";
    };
  }, [isOpen]);

  return (
    <section className="proj-section" id="gallery">
      <div className="proj-container">
        <div className="proj-header">
          <div className="proj-header-left">
            <p className="proj-kicker">RECENT WORK</p>
            <h2 className="proj-title">Project Gallery</h2>
            <p className="proj-subtitle">
              Real craftsmanship. Real results. Browse a few recent projects below.
            </p>
          </div>
          <div className="proj-header-right">
            <button className="proj-header-btn proj-header-prev" onClick={() => window.dispatchEvent(new CustomEvent('proj-prev'))} aria-label="Previous">
              <FiChevronLeft />
            </button>
            <button className="proj-header-btn proj-header-next" onClick={() => window.dispatchEvent(new CustomEvent('proj-next'))} aria-label="Next">
              <FiChevronRight />
            </button>
          </div>
        </div>

        <div className="proj-sliderWrap">
          <Swiper
            modules={[Navigation, Pagination, Keyboard]}
            pagination={{ clickable: true }}
            keyboard={{ enabled: true }}
            spaceBetween={16}
            slidesPerView={1.1}
            breakpoints={{
              520: { slidesPerView: 1.4 },
              768: { slidesPerView: 2.2 },
              1024: { slidesPerView: 3 },
            }}
            className="proj-swiper"
            onBeforeInit={(swiper) => {
              window.addEventListener('proj-prev', () => swiper.slidePrev());
              window.addEventListener('proj-next', () => swiper.slideNext());
            }}
          >
            {projectImages.map((img, index) => (
              <SwiperSlide key={img.src}>
                <button
                  type="button"
                  className="proj-card"
                  onClick={() => openLightbox(index)}
                  aria-label={`Open project image ${index + 1}`}
                >
                  <img className="proj-img" src={img.src} alt={img.alt} loading="lazy" />

                  <div className="proj-hover">
                    <span className="proj-eyeBtn" aria-hidden="true">
                      <FiEye />
                    </span>
                  </div>
                </button>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>

      {/* LIGHTBOX */}
      {isOpen && activeImage && (
        <div className="proj-lightbox" role="dialog" aria-modal="true">
          <button className="proj-close" onClick={closeLightbox} aria-label="Close preview">
            <FiX />
          </button>

          <button className="proj-nav proj-prev" onClick={prevImage} aria-label="Previous image">
            <FiChevronLeft />
          </button>

          <div className="proj-previewWrap" onClick={closeLightbox}>
            <div className="proj-previewInner" onClick={(e) => e.stopPropagation()}>
              <img className="proj-previewImg" src={activeImage.src} alt={activeImage.alt} />
            </div>
          </div>

          <button className="proj-nav proj-next" onClick={nextImage} aria-label="Next image">
            <FiChevronRight />
          </button>
        </div>
      )}
    </section>
  );
}
