import React from "react";
import "./TestimonialsSlider.css";

import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Keyboard, Autoplay } from "swiper/modules";

import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

import { FiStar, FiMessageSquare, FiChevronLeft, FiChevronRight } from "react-icons/fi";
import { FaQuoteLeft } from "react-icons/fa";

type Testimonial = {
  name: string;
  location: string;
  message: string;
  rating: number;
};

const testimonials: Testimonial[] = [
  {
    name: "Sarah M.",
    location: "Waterville, ME",
    message:
      "Great communication and excellent craftsmanship. Our deck looks amazing and the cleanup was spotless.",
    rating: 5,
  },
  {
    name: "James R.",
    location: "Plymouth, ME",
    message:
      "Fast response, honest pricing, and the work was done right the first time. They repaired rot damage and it looks brand new.",
    rating: 5,
  },
  {
    name: "Emily T.",
    location: "Skowhegan, ME",
    message:
      "We had siding repairs and a new door installed. Everything feels solid and looks professional. Very easy to work with.",
    rating: 5,
  },
  {
    name: "Michael K.",
    location: "Bangor, ME",
    message:
      "Reliable and skilled. You can tell they care about quality. Well definitely call again for future home projects.",
    rating: 5,
  },
  {
    name: "Lisa P.",
    location: "Newport, ME",
    message:
      "Smooth process from quote to finish. Great attention to detail and the results exceeded expectations.",
    rating: 5,
  },
];

function Stars({ count }: { count: number }) {
  return (
    <div className="tst-stars" aria-label={`${count} out of 5 stars`}>
      {Array.from({ length: 5 }).map((_, i) => (
        <span
          key={i}
          className={`tst-star ${i < count ? "is-filled" : ""}`}
          aria-hidden="true"
        >
          <FiStar />
        </span>
      ))}
    </div>
  );
}

export default function TestimonialsSlider() {
  const swiperRef = React.useRef(null);

  const handlePrev = () => {
    swiperRef.current?.swiper?.slidePrev();
  };

  const handleNext = () => {
    swiperRef.current?.swiper?.slideNext();
  };

  return (
    <section className="tst-section" id="testimonials">
      <div className="tst-container">
        <div className="tst-header">
          <div className="tst-header-left">
            <p className="tst-kicker">TESTIMONIALS</p>
            <h2 className="tst-title">What Homeowners Say</h2>
            <p className="tst-subtitle">
              Real feedback from customers across Central Maine. Quality work and
              honest service, every time.
            </p>
          </div>
          <div className="tst-header-right">
            <button className="tst-header-btn tst-header-prev" onClick={handlePrev} aria-label="Previous">
              <FiChevronLeft />
            </button>
            <button className="tst-header-btn tst-header-next" onClick={handleNext} aria-label="Next">
              <FiChevronRight />
            </button>
          </div>
        </div>

        <div className="tst-sliderWrap">
          <Swiper
            ref={swiperRef}
            modules={[Navigation, Pagination, Keyboard, Autoplay]}
            pagination={{ clickable: true }}
            keyboard={{ enabled: true }}
            autoplay={{ delay: 4500, disableOnInteraction: true }}
            spaceBetween={16}
            slidesPerView={1.05}
            breakpoints={{
              520: { slidesPerView: 1.3 },
              768: { slidesPerView: 2 },
              1024: { slidesPerView: 3 },
            }}
            className="tst-swiper"
          >
            {testimonials.map((t, idx) => (
              <SwiperSlide key={`${t.name}-${idx}`}>
                <article className="tst-card">
                  <div className="tst-cardTop">
                    <div className="tst-quoteIcon" aria-hidden="true">
                      <FaQuoteLeft />
                    </div>

                    <Stars count={t.rating} />
                  </div>

                  <p className="tst-message">{t.message}</p>

                  <div className="tst-footer">
                    <div className="tst-avatar" aria-hidden="true">
                      <FiMessageSquare />
                    </div>

                    <div className="tst-person">
                      <p className="tst-name">{t.name}</p>
                      <p className="tst-location">{t.location}</p>
                    </div>
                  </div>
                </article>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>

        <div className="tst-ctaStrip">
          <div className="tst-ctaText">
            <h3>Want results like these?</h3>
            <p>Get a fast, free estimate for your next home project.</p>
          </div>

          <a className="tst-ctaBtn" href="/contact">
            Get Free Estimate
          </a>
        </div>
      </div>
    </section>
  );
}
